







































































































import { Component, Prop, Vue, Watch } from 'vue-property-decorator';

@Component
export default class SideNavigation extends Vue {

  $t: any;
  appIcon: string = 'https://commander-development-images.s3-us-west-1.amazonaws.com/commandeer-logo.png';
  title: string = this.$t('commandeer');

  get items() {
    return [{
      title: `${this.$t('dashboard')}`,
      to: { 'name': 'homePage' },
      active: true,
    }, {
      title: `${this.$t('tanks')}`,
      to: { 'name': 'tanksPage' },
      active: true,
    }, {
      title: `${this.$t('battles')}`,
      to: { 'name': 'battlePage' },
      active: true,
    }];
  }

  get topSectionItems() {
    return [
      { title: 'docs', to: 'https://getcommandeer.com/docs' },
      { title: 'submit_a_service', to: 'https://getcommandeer.com/docs/openSource/submitService' },
    ];
  }

  async mounted() {}

}
